import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad() {
		this.flist = [];
		this.slist = [];
		this.tlist = [];
		uni.showLoading({
			title: '加载中..'
		})
		this.getClassInfo();
	},
	onShow() {


	},
	computed: {
		...mapState(['token'])
	},
	methods: {
		getClassInfo() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/good_type/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode)
					// 封装父级list
					res.data.forEach(item => {
						let temp_item = {
							id: item.id,
							name: item.type_name
						}
						this.flist.push(temp_item);
						// 封装子级别list
						let temp_item_s = {
							id: item.id,
							pid: item.id,
							name: item.type_name
						}
						this.slist.push(temp_item_s);
						// 封装三级list
						item.brand.forEach(itemt => {
							let brandList = {
								id: itemt.id,
								pid: item.id,
								name: itemt.brand_name,
								picture: itemt.brand_picture,
								detail_url: itemt.url
							}
							this.tlist.push(brandList);

						})

					});
					uni.hideLoading();
				}
			});

		},

		//一级分类点击
		tabtap(item) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}

			this.currentId = item.id;
			let index = this.slist.findIndex(sitem => sitem.pid === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(detail_url, title_name) {
			var data = {
				detail_url: detail_url,
				title_name: title_name
			}
			uni.navigateTo({
				url: '/pages/product/list?data=' + JSON.stringify(data)

			})
		}
	}
}
